.button {
  display: inline-block;
  background-color: #408F95;
  color: #fff;
  font-weight: 400;
  font-family: "Gotu", sans-serif;
  line-height: 1.571;
  width: 12.625rem;
  font-size: 0.875rem;
  position: relative;
  transition: all 0.3s;
  border: 0.0625rem solid #408F95;
  z-index: 3;
  padding: 1rem;
  cursor: pointer;
  text-align: center;
}
.button:hover::after {
  transform: scale(1, 1);
}

.button::before {
  content: "";
  border: 1px solid #408F95;
  position: absolute;
  bottom: -0.3125rem;
  right: -0.3125rem;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.button::after {
  background-color: #fff;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: all 0.3s;
  z-index: 2;
}

.button:hover,
.button:hover span::after {
  color: #408F95;
  border-bottom: 1px solid #408F95;
  border-right: 1px solid #408F95;
  opacity: 1;
}

.button span {
  display: inline-block;
  padding-bottom: 0.3125rem;
  position: relative;
  z-index: 3;
}

.button span::after {
  content: "";
  display: inline-block;
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
  margin-left: 0.375rem;
  margin-bottom: 0.25rem;
}

.button.button--white {
  background-color: #fff;
  color: #408F95;
  border: 0.0625rem solid #fff;
}

.button.button--white::before {
  border: 1px solid #fff;
}

.button.button--white::after {
  background-color: #408F95;
}

.button.button--white:hover,
.button.button--white:hover span::after {
  color: #fff;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
}

.button.button--white span::after {
  border-bottom: 1px solid #408F95;
  border-right: 1px solid #408F95;
}

